.infinite-text-container {
  position: relative;
  background-color: #f4f7fe;
  overflow: hidden;

  .infinite-text {
    // transform: skewY(357deg) translateY(-5px);
    z-index: 2;
    width: 100%;
    background: inherit;
    // font-family: $montserrat;

    &:nth-child(2) {
      .line {
        animation: animate3 80s linear infinite;
        animation-delay: -80s;

        &:nth-child(2) {
          animation: animate4 80s linear infinite;
          animation-delay: -40s;
        }
      }
    }

    .line {
      color: $clr1;
      background-color: $clr2;
      font-weight: bold;
      // font-family: poppins-bold, sans-serif;
      font-size: clamp(rem(32px), 4vw, rem(64px));
      white-space: nowrap;
      text-transform: uppercase;
      animation: animate 80s linear infinite;
      animation-delay: -80s;

      &:nth-child(2) {
        animation: animate2 80s linear infinite;
        animation-delay: -40s;
      }

      span {
        -webkit-text-stroke: 1.5px $clr1;
        color: transparent;
        font-weight: inherit;
      }
    }
  }
}

@keyframes animate {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes animate2 {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-200%);
  }
}

@keyframes animate3 {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes animate4 {
  0% {
    transform: translateX(-200%);
  }
  100% {
    transform: translateX(0%);
  }
}

.project-list {
  z-index: 2;
  background: $clr2;
  position: relative;

  ::-moz-selection {
    color: $clr2;
    background: $clr1;
  }
  ::selection {
    color: $clr2;
    background: $clr1;
  }

  .project {
    flex-wrap: wrap;
    flex-direction: column;
    gap: 40px;
    min-height: calc(100dvh - 80px);
    align-items: center;
    justify-content: center;

    color: $clr4;
    // background: $clr2;
    padding-bottom: 200px;
    padding: 20px 0 130px;

    &:first-child {
      margin-top: 30px;
      @include for-normal-layout {
        margin-top: 50px;
      }
    }

    @include for-wide-layout {
      flex-direction: row;
      padding: 20px 30px 200px;

      &:nth-child(2n) {
        flex-direction: row-reverse;
      }
    }

    .project-info {
      padding: 20px;
      align-items: flex-start;

      @include for-wide-layout {
        flex: 3;
        padding: 0;
      }

      h5 {
        text-transform: uppercase;
        margin-bottom: 5px;
      }
      h2 {
        font-weight: 700;
        margin-bottom: 20px;
        font-size: clamp(rem(24px), 2vw, rem(32px));
      }

      .desc {
        font-size: clamp(rem(16px), 2.5vw, rem(18px));
        @include for-wide-layout {
          flex: 1;
        }
      }

      .skills-used {
        gap: 1.125rem;
        margin-top: 20px;
        flex-wrap: wrap;

        svg {
          height: 40px;

          @include for-normal-layout {
            height: 50px;
          }
        }
      }

      .project-actions {
        gap: 0.5rem;
        margin-top: 20px;

        .action {
          color: $clr1;
          padding: 10px;
          border-radius: 10px;
          border: 2px solid $clr1;
          // box-shadow: 0px 17px 10px -10px;
          transition: box-shadow 0.3s ease-in-out, transform 0.2s ease-in-out;

          &:nth-child(1) {
            color: $clr2;
            background: $clr1;
          }

          &:hover {
            box-shadow: 0px 18px 15px -10px $clr1;
            transform: translate(0px, -5px) scale(1.1);
          }
        }
      }
    }

    .gallery {
      width: 100%;
      max-width: 960px;
      position: relative;
      flex: 4;

      > .image-container {
        position: relative;
        overflow: hidden;
        display: flex;

        > img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          transition: scale 0.2s cubic-bezier(0.22, 0.61, 0.36, 1);

          &:hover {
            scale: 1.05;
          }
        }

        &:has(.mobile-image) {
          border-radius: 10px;
          border: 3px solid $clr2;
          position: absolute;
          height: 80%;
          object-fit: contain;
          width: unset;
          translate: 25% 60%;
          left: 0;
          z-index: 2;
        }
      }

      .mobile-image {
        height: 100%;
      }
      .desktop-image {
      }
    }

    &:nth-child(2n) .gallery .image-container:has(.mobile-image) {
      right: 0;
      left: unset;
      translate: -25% 60%;
    }
  }
}
